<template>
    <Transition name="modal"> 
    <div class="modal" @click="closeModal">
        <div class="modal__block" @click.stop="">
            <h2 class="modal__block_title">{{ edit ? 'Изменить заметку' : 'Добавить заметку' }}</h2>
            
            <label>
                <span>Title</span>
                <input v-model="title" type="text" placeholder="Введи название заметки">
            </label>
            <label>
                <span>Content</span>
                <textarea v-model="text" placeholder="Введите текст заметки"></textarea>
            </label>
            
            <div class="modal__block_btns">
                <button class="modal__block_btns-btn del" @click="closeModal">Отмена</button>
                <button v-if="!edit"  class="modal__block_btns-btn edit" @click="addNote">Добавить</button>
                
                <button v-else class="modal__block_btns-btn edit" @click="changeNote">Изменить</button>
            </div>
        </div>
    </div>
</Transition>
</template>

<script>
import { v4 as uuidv4} from 'uuid'

    export default {
        props:{
            edit:{
                typeof: Boolean
            },
            editedNote:{
                typeof: Object
            }
        },
        data() {
            return {
                title: '',
                text: ''
            }
        },
        methods: {
            closeModal(){
                this.$emit('close')
                this.title = this.text = ''
            },
            
            addNote(){
                if (this.title != '' && this.text != '') {
                    const note = {
                    id: uuidv4(),
                    title: this.title,
                    text: this.text,
                    // 8.05.23
                    date:new Date().toLocaleDateString()
                }
                this.$emit('addNote',note)
                this.closeModal()}
            },
            
            changeNote(){
                if (this.title != '' && this.text != '') {
                    const newEditedNote = {
                    id: this.editedNote.id,
                    title: this.title,
                    text: this.text,
                    // 8.05.23
                    date:new Date().toLocaleDateString()
                }
                this.$emit('changedNote',newEditedNote)
                this.closeModal()}
            }
        },
   
    }
</script>

